body {
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
}

.stopwatch__center-circle {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.stopwatch__minute-hand, .stopwatch__second-hand {
  transform-origin: 100% 100%;
  animation-iteration-count: infinite;
  position: absolute;
  bottom: 50%;
  left: 50%;
}

.stopwatch__minute-hand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite rotate-minutes;
}

.stopwatch__second-hand {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-seconds;
}

.stopwatch--speed-up .stopwatch__minute-hand {
  animation: 600s steps(60, end) infinite rotate-stopwatch;
}

.stopwatch--speed-up .stopwatch__second-hand {
  animation: 10s linear infinite rotate-stopwatch;
}

@keyframes rotate-stopwatch {
  0% {
    transform: translateX(-50%)rotate(0);
  }

  100% {
    transform: translateX(-50%)rotate(360deg);
  }
}
